}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive;
  --white-color: #fff;
  --secondary-white-color: #fafafa;
  --black-color: #000;
  --light-grey-color: #e1e1e1;
  --secondary-light-grey-color: #d9d9d9;
  --grey-color: #907e82;
  --secondary-grey-color: #ecebeb;
  --light-pink-color: #fff5f6;
  --pink-color: #ffa5ba;
  --secondary-pink-color: #ffb8ca;
  --icon-pink-color: #fea5bb;
  --dark-pink-color: #e17992;
  --secondary-dark-pink-color: #e18298;
  --red-pink-color: #d41443;
  --secondary-red-pink-color: #d41342;
  --cream-color: #f0d1a5;
  --light-green-color: #c2e297;
  --anim-duration: 250ms;
  --anim-timing-function: cubic-bezier(.4, 0, .2, 1);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

